import axios from 'axios';
import { getIpAddress } from './getIpAddress';


function getMacRecord (mac) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/genius/', { 'data': { 'mac': mac }, 'service': 'get-mac-record' });
  // return axios.post('http://10.167.16.43:8000/genius/get-mac-record/', { 'data': { 'mac': mac }, 'service': 'get-mac-record' });
}

function getOuiList () {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/genius/', { 'service': 'get-oui-list' });
}

function getBuList () {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/genius/', { 'service': 'get-bu-list' });
  // return axios.post('http://10.167.16.43:8000/genius/get-bu-list/');
}

function getMacAccess (username) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/genius/', { 'data': { 'username': username }, 'service': 'get-mac-access' });
  // return axios.post('http://10.167.16.43:8000/genius/get-mac-access/', { 'username': username });
}

function getMacBlock () {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/genius/', { 'service': 'get-mac-block' });
  // return axios.post('http://10.167.16.43:8000/genius/get-mac-block/');

}

function addMacBlock (data) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/genius/', { 'data': data, 'service': 'add-mac-block' });
  // return axios.post('http://10.167.16.43:8000/genius/add-mac-block/', data);
}

function disableMacBlock (data) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/genius/', { 'data': data, 'service': 'disable-mac-block' });
  // return axios.post('http://10.167.16.43:8000/genius/disable-mac-block/', data);
}

export {
  getMacRecord,
  getOuiList,
  getBuList,
  getMacAccess,
  getMacBlock,
  addMacBlock,
  disableMacBlock,
};


