import axios from 'axios';
import { getIpAddress } from './getIpAddress';

function getBasic (sernum, month) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/get-record-by-sernum/', { 'sernum': sernum, 'month': month });
}

export {
  getBasic
};


