import axios from 'axios';
import { getIpAddress } from './basic';

function getStationPage (token) {
  const ipAddress = getIpAddress();
  return axios.get(ipAddress + '/get-station-page/?token=' + token);
}

function getContainerPage (station_name, token) {
  const ipAddress = getIpAddress();
  return axios.get(ipAddress + '/get-container-page/?station_name=' + station_name + '&token=' + token);
}

export {
  getStationPage,
  getContainerPage
};



