<template>
  <v-content id="gac-register" class="primary">
      <v-container fluid fill-height>
        <v-layout justify-center>
          <v-flex xs12 sm8 md6 lg6>
            <v-card class="elevation-1 pa-3">
              <v-card-text>
                <div class="layout column align-center">
                  <!-- <img src="/static/vue.png" alt="Vue Material Admin" width="120" height="120"> -->
                  <h1 class="flex my-4 primary--text">Register GAC</h1>
                </div>
                <v-layout row wrap>
                  <v-flex xs5 sm5 md5 lg5>
                  <v-text-field append-icon="person" label="Username" type="text" :rules="rules" v-model="model.username"></v-text-field>
                  </v-flex>
                  <v-flex offset-xs2 xs5 sm5 md5 lg5>
                  <v-text-field append-icon="lock" label="Password(need to reset at first login)" type="password" :rules="rules" v-model="model.password"></v-text-field>
                  </v-flex>
                  <v-flex xs5 sm5 md5 lg5>
                  <v-text-field append-icon="lock" label="First Name" type="text" v-model="model.first_name"></v-text-field>
                  </v-flex>
                  <v-flex offset-xs2 xs5 sm5 md5 lg5>
                  <v-text-field append-icon="lock" label="Last Name" type="text" v-model="model.last_name"></v-text-field>
                  </v-flex>
                  <!-- <v-flex xs12 sm12 md12 lg12>
                  <v-text-field append-icon="lock" label="Chinese Name(Optional)" type="text" v-model="model.chinese_name"></v-text-field>
                  </v-flex> -->
                  <v-flex xs12 sm12 md12 lg12>
                    <v-menu
                      class="pr-2"
                      ref="setStatDate"
                      lazy
                      :close-on-content-click="false"
                      v-model="startDateMenu"
                      transition="scale-transition"
                      offset-y
                      full-width
                      :nudge-bottom="-22"
                      max-width="290px"
                      :return-value.sync="model.onboard"
                    >
                      <v-text-field
                        slot="activator"
                        label="On Board Day(YYYY-MM-DD)"
                        v-model="model.onboard"
                        append-icon="event"
                        readonly
                      ></v-text-field>
                      <v-date-picker v-model="model.onboard" no-title scrollable>
                        <v-spacer></v-spacer>
                        <v-btn flat color="primary" @click="startDateMenu = false">Cancel</v-btn>
                        <v-btn flat color="primary" @click="$refs.setStatDate.save(model.onboard)">OK</v-btn>
                      </v-date-picker>
                    </v-menu>
                  </v-flex>
                  <v-flex xs12 sm12 md12 lg12>
                  <v-text-field append-icon="lock" label="Identifier(used to reset passwrod)" type="text" :rules="rules" v-model="model.identifier"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-card-text>
              <v-card-actions>
                <v-btn block color="primary" @click="registerSubmit" :loading="loading">Register</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
          <v-dialog
            v-model="openDialog"
            max-width="590"
          >
          <v-card>
            <v-card-text>
              <h4>{{ message }}</h4>
            </v-card-text>
          </v-card>
        </v-dialog>

        </v-layout>
      </v-container>
  </v-content>
</template>

<script>
import { getRegister } from '../api/gac';

export default {
  data: () => ({
    loading: false,
    startDateMenu: false,
    message: '',
    openDialog: false,
    rules: [v => v.length > 5 || 'Min 6 characters'],
    model: {
      username: '',
      password: '',
      first_name: '',
      last_name: '',
      chinese_name: '',
      onboard: '',
      identifier: '',
    }
  }),
  methods: {
    registerSubmit () {
      this.message = '';
      if (this.model.username.length < 6) {
        this.message = 'Username length should be more than 5';
      }
      if (this.model.username.length === 'genius') {
        this.message = 'Username should NOT be genius';
      }
      if (this.model.username.length === 'root') {
        this.message = 'Username should NOT be root';
      }
      if (this.model.password.length < 6) {
        this.message = 'Password length should be more than 5';
      }
      if (this.model.onboard.length < 6) {
        this.message = 'On Board Day length should be more than 5';
      }
      if (this.model.identifier.length < 6) {
        this.message = 'Identifier length should be more than 5';
      }
      if (this.message) {
        this.openDialog = true;
        return false;
      }
      this.loading = true;
      getRegister(this.model.username, this.model.password, this.model.first_name, this.model.last_name, this.model.chinese_name, this.model.onboard, this.model.identifier)
        .then(response => {
          this.openDialog = true;
          this.loading = false;
          if (response.data.status) {
            this.message = response.data.payload.message;
          } else {
            this.message = response.data.payload.message;
            return false;
          }
          setTimeout(() => {
            this.$router.push('/genius/login');
          }, 5000);
          
        })
        .catch(e => {
          // console.log(e);
          this.openDialog = true;
          this.loading = false;
          this.message = 'GAC Service Errors';
        });
    },
  }

};
</script>
<style scoped lang="css">
  #gac-register {
    height: 50%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    z-index: 0;
  }
</style>



