import { getIpAddress } from '../../pages/genius/api/basic';

let hostname = getIpAddress().split('/')[2];

const extraMenu = [
  {
    title: 'Local Logs',
    icon: 'view_compact',
    target: '_blank',
    href: 'https://' + hostname + '/logs',
  },
  {
    title: 'Local Config',
    icon: 'view_compact',
    target: '_blank',
    href: 'https://' + hostname + '/config',
  },
  {
    title: 'Local Prod Code',
    icon: 'view_compact',
    target: '_blank',
    href: 'https://' + hostname + '/prod',
  },
  {
    title: 'Local te Code',
    icon: 'view_compact',
    target: '_blank',
    href: 'https://' + hostname + '/te',
  },
];

export default extraMenu;


