const genius_router = [
  {
    path: '/genius',
    component: () => import('./Genius.vue'),
    meta: {
      // theme: 'purple',  // purple
    },
    children: [
      {
        path: 'login',
        meta: {
          breadcrumb: true,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/genius/pages/login.vue`
        )
      },
      {
        path: 'logout',
        meta: {
          breadcrumb: true,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/genius/pages/logout.vue`
        )
      },
      {
        path: 'manage',
        meta: {
          breadcrumb: false,
          requireAuth: true,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/genius/pages/manage.vue`
        )
      },
      {
        path: '/genius',
        meta: {
          breadcrumb: false,
          requireAuth: true,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/genius/pages/station.vue`
        )
      },
      {
        path: ':container',
        meta: {
          breadcrumb: false,
          requireAuth: true,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/genius/pages/container.vue`
        )
      },
      {
        path: ':container/:connection',
        meta: {
          breadcrumb: false,
          requireAuth: true,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/genius/pages/connection.vue`
        )
      },
    ]
  },
];

export default genius_router;


