const gac_router = [
  {
    path: '/account',
    component: () => import('./Gac.vue'),
    meta: {
      // theme: 'indigo',
    },
    children: [
      {
        path: 'register',
        meta: {
          breadcrumb: false
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/gac/pages/Register.vue`
        )
      },
      {
        path: 'reset-password',
        meta: {
          breadcrumb: false,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/gac/pages/ResetPassword.vue`
        )
      },
      {
        path: 'change-password',
        meta: {
          breadcrumb: false,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/gac/pages/ChangePassword.vue`
        )
      },
      {
        path: 'set-identifierxxx',
        meta: {
          breadcrumb: false,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/gac/pages/Identifier.vue`
        )
      },
    ]
  },
];

export default gac_router;



