<template>
  <v-widget :title="title">
    <div slot="widget-content">
      <v-layout row wrap>   
        <v-flex xl12 pa-0>
          <v-card>
            <v-toolbar card color="white">
              <v-text-field
              flat
              solo
              prepend-icon="search"
              placeholder="Filtering"
              v-model="searchFilter"
              hide-details
              class="hidden-sm-and-down"
              ></v-text-field>             
            </v-toolbar>
            <v-divider></v-divider>
            <v-card-text class="pa-0">
              <v-data-table
                :headers="testData.headers"
                :pagination.sync="testData.pagination"
                :search="searchFilter"
                :items="testDataContent"
                :rows-per-page-items="[10,25,50,{text:'All','value':-1}]"
                class="elevation-1"
                item-key="recttime"
                >
                <template slot="items" slot-scope="props">
                  <tr>
                    <td>{{ props.item.id }}</td>
                    <td>{{ props.item.created_at.replace('T', ' ').split('.')[0] }}</td>
                    <td>{{ props.item.mac_start }}</td>
                    <td>{{ props.item.mac_end }}</td>
                    <td>{{ props.item.block }}</td>
                    <td>{{ props.item.bu }}</td>
                    <td>{{ props.item.project }}</td>
                    <td>{{ props.item.product }}</td>
                    <td>{{ props.item.mac_start2 }}</td>
                    <td>{{ props.item.block_remain }}/{{ props.item.block_set }}={{ props.item.block_remain/props.item.block_set }}</td>
                    <td>{{ props.item.username }}</td>
                    <td>{{ (props.item.disabled) && "YES" || "NO" }}</td>
                    <td>{{ props.item.updated_at.replace('T', ' ').split('.')[0] }}</td>
                    <td v-show="!props.item.username1"><v-icon v-show="!props.item.disabled" @click="disableMacBlockData(props.item)">delete</v-icon></td>
                    <td v-show="props.item.username1">{{ props.item.username1 }}</td>

                  </tr>
                </template>
              </v-data-table>
              <json-to-excel :jsonData="testDataSource"></json-to-excel>
            </v-card-text>
          </v-card>
        </v-flex>             
      </v-layout>
        <!-- Disable MAC Block comfirmation dialogs -->
        <v-layout row justify-center>
          <v-dialog v-model="openDialogs" persistent max-width="800">
            <v-card>
              <v-card-title class="headline">Are You Sure to Disable The MAC Block???</v-card-title>
              <v-spacer></v-spacer>
              <v-card-text>
                <h4>MAC Information: </h4>
                <h4>{{ macInfo }}</h4>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="green darken-1" flat @click="openDialogs = false">Cancel</v-btn>
                <v-btn color="green darken-1" flat @click="disableConfirm = true; disableMacBlockData(macBlock)">Confirm</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-layout>
    </div>
  </v-widget>
</template>

<script>
import VWidget from '@/components/VWidget';
import JsonToExcel from '../components/JsonToExcel';
import store from '../../vision/store';

import { disableMacBlock } from '../api/mac';


export default {
  components: {
    VWidget,
    JsonToExcel,
    store,
  },
  props: ['title', 'testDataSource', 'username'],
  data () {
    return {
      macBlock: null,
      macInfo: '',
      disableConfirm: false,
      message: '',
      openDialogs: false,
      searchFilter: '',
      testData: {
        pagination: {
          descending: true,
        },
        headers: [
          {
            text: 'ID',
            value: 'id',
          },
          {
            text: 'Created At',
            value: 'created_at',
          },
          {
            text: 'MAC Start',
            value: 'mac_start'
          },
          {
            text: 'MAC End',
            value: 'mac_end'
          },
          {
            text: 'Block Size',
            value: 'block'
          },
          {
            text: 'BU',
            value: 'bu'
          },
          {
            text: 'Project',
            value: 'project'
          },
          {
            text: 'Product',
            value: 'product'
          },

          {
            text: 'MAC Start2',
            value: 'mac_start2'
          },
          {
            text: 'Block Remain',
            value: 'block_remain'
          },
          {
            text: 'Username',
            value: 'username'
          },          
          {
            text: 'Disabled',
            value: 'disabled'
          },
          {
            text: 'Updated At',
            value: 'updated_at'
          },
          {
            text: 'Disabled By',
            value: ''
          },
        ],
        content: []
      },
    };
  },
  computed: {
    testDataContent () {
      return this.testDataSource || [];
    },
  },

  methods: {
    disableMacBlockData (data) {
      this.macBlock = data;
      this.openDialogs = true;

      this.macInfo = this.macBlock['bu'] + ', ' + this.macBlock['project'] + ', ' + this.macBlock['product'] + ', ' + this.macBlock['mac_start'] + ', ' + this.macBlock['mac_end'] + ', ' + this.macBlock['block'];
      if (!this.disableConfirm) {
        return false;
      }
      this.disableConfirm = false;
      let data1 = {
        'bu': this.macBlock['bu'],
        'project': this.macBlock['project'],
        'product': this.macBlock['product'],
        'mac_start': this.macBlock['mac_start'],
        'username': this.username,
      };
      this.openDialogs = false;
      disableMacBlock(data1)
        .then(response => {
          // console.log(response.data);
          this.$emit('disableMac', response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
  }
};
</script>

<style lang='stylus' scoped>
</style>


