<template>
  <v-app id="gac-login" class="primary">
    <v-content>
      <v-container fluid fill-height>
        <v-layout justify-center>
          <v-flex xs12 sm8 md8 lg6>
            <v-card class="elevation-1 pa-3">
              <v-card-text>
                <div class="layout column align-center">
                  <!-- <img src="/static/logo.png" alt="Vue Material Admin" width="120" height="120"> -->
                  <h1 class="flex my-4 primary--text">Genius Login</h1>
                  <h5 class="flex error--text">{{ error }}</h5>
                </div>
                <v-form>
                  <v-text-field 
                  append-icon="person" 
                  name="login" 
                  placeholder="Username" 
                  type="text"
                  v-model="model.username" 
                  ></v-text-field>
                  <v-text-field 
                  append-icon="lock" 
                  name="password" 
                  placeholder="Password" 
                  id="password" 
                  type="password" 
                  v-model="model.password"
                  v-on:keyup.enter="validatePassword"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-btn block color="primary" 
                @click="validatePassword" 
                :loading="loading">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import { 
  validateUsernameApi,
  validatePasswordApi,
} from '../api/gac';
import { getServerName } from '../api/getServerName';
const crypto = require('crypto');

export default {
  data: () => ({
    error: '',
    salt: '',
    next: '',
    loading: false,
    model: {
      username: '',
      password: ''
    }
  }),
  created () {
    this.$cookies.remove('username');
    this.$cookies.remove('role');
    this.$cookies.remove('token');
    this.$cookies.remove('pid');
    const params = this.$route.query;
    if (params.next) {
      this.next = params.next;
    }
    // console.log(this.next);
    // this.$router.push(this.next);
  },
  mounted () {
    // change title here
    this.getCurrentServerName();
  },
  methods: {
    validatePassword () {
      this.error = '';
      this.loading = true;
      validateUsernameApi(this.model.username)
        .then(response => {
          if (response.data.status) {
            this.salt = response.data.payload.data;
            if (this.salt === '') {
              this.error = 'GAC Background Service Error';
              this.loading = false;
              return false;
            }
            this.$cookies.set('pid', this.salt, '12h');
            let password1 = this.ssha_pass(this.model.password, this.salt);
            validatePasswordApi(this.model.username, password1)
              .then(response => {
                // console.log(response.data.payload);
                if (response.data.status) {
                  this.error = '';
                  // console.log('Login Successfully');
                  // console.log(response.data.payload.data);
                  let username = response.data.payload.data.username;
                  let updated_at = response.data.payload.data.updated_at;
                  let expire = response.data.payload.data.expire;
                  let role = response.data.payload.data.role;
                  let token = response.data.payload.token;

                  this.$cookies.set('username', username, '12h');
                  this.$cookies.set('role', role, '12h');
                  this.$cookies.set('token', token, '12h');

                  if (updated_at === '') {
                    this.error = 'Should Update Identifier Now!!!';
                    setTimeout(() => {
                      this.$router.push('/account/set-identifierxxx');
                    }, 3000);
                    return false;
                  }
                  if (expire > 0) {
                    this.error = 'It is time to change password.';
                  }
                  
                  window.getApp.$emit('APP_LOGIN_SUCCESS');
                  setTimeout(() => {
                    if (expire > 0) {
                      this.$cookies.remove('role');
                      this.$cookies.remove('token');
                      this.$cookies.remove('pid');
                      this.$router.push('/account/change-password');
                    }
                    else if (this.next) {
                      this.$router.push(this.next);
                    } else {
                      this.$router.push('/genius');
                    }
                  }, 3000);
                } else {
                  this.error = response.data.payload.message;
                  this.loading = false;
                }
              })
              .catch(e => {
                console.log(e);
                this.error = 'GAC Service2 Error';
                this.loading = false;
              });
          } else {
            this.error = response.data.payload.message;
            this.loading = false;
          }
        })
        .catch(e => {
          console.log(e);
          this.error = 'GAC Service Error';
          return false;
        });
    },
    ssha_pass (passwd, salt) {
      if (salt === 'dummy') {
        return passwd;
      }
      let ctx = crypto.createHash('sha1');
      ctx.update(passwd, 'utf-8');
      ctx.update(salt, 'binary');
      let digest = ctx.digest('binary');
      let ssha = '{SSHA}' + Buffer.from(digest + salt, 'binary').toString('base64');
      // console.log(ssha);
      return ssha;
    },
    getCurrentServerName () {
      getServerName()
        .then(response => {
          this.serverName = response.data.payload.data;
          document.title = this.serverName + ' | Genius';
          // console.log(this.serverName);
        })
        .catch(e => {
          console.log(e);
        });
    },
  }

};
</script>
<style scoped lang="css">
  #gac-login {
    height: 50%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    z-index: 0;
  }
</style>



