<template>
  <v-layout row wrap>
    <v-flex sm12>
      <v-widget title="MAC Address/Serial Number Search ">
        <div slot="widget-content">
          <v-flex>
            <v-alert
              v-model="alert"
              dismissible
              :color="alert_color"
            >
              {{ alert_message }}
            </v-alert>
          </v-flex>
          <v-container fluid>
            <v-layout row>
              <v-flex xl4>
                <v-text-field
                  name="macaddress"
                  label="MAC Address/Serial Number"
                  value=""
                  v-model="mac"
                  color="primary"
                  class="input-group--focused"
                ></v-text-field>
              </v-flex>
              <v-flex xl4>
                <v-btn large
                  color="primary"
                  :loading="loading"
                  @click.native="loading = true"
                  @click.exact="getTestData"
                  :disabled="loading"
                >
                  Search
                  <span slot="loader">Loading...</span>
                </v-btn>
              </v-flex>
              <v-flex xl1>
                  <v-btn large
                  v-show="access"
                  color="primary"
                  href="#/vision/manage-mac"
                >
                  MAC Management
                </v-btn>
              </v-flex>
            </v-layout>
            <v-flex xl12>

              <v-card flat>
                <mac-table
                  id="snfind-table"
                  title="Mac Record Details"
                  search=""
                  :testDataSource="dataSource"
                ></mac-table>
              </v-card>

            </v-flex>             
          </v-container>
        </div>
      </v-widget>
    </v-flex>
  </v-layout>
</template>

<script>
import VWidget from '@/components/VWidget';
import { getMacRecord } from '../api/mac';
import MacTable from '../components/MacTable';

import store from '../store';

export default {
  components: {
    VWidget,
    MacTable,
    store,
  },
  data () {
    return {
      username: '',
      access: true,
      loading: false,
      // for alert
      alert: false,
      alert_color: 'warning',
      alert_message: 'it is error',
      dataSource: [],
      mac: '',
      //
    };
  },

  created () {
    this.username = this.$cookies.get('username');
    if (!this.username) {
      this.access = false;
    }
    if (this.username === 'genius') {
      this.access = false;
    }
  },

  methods: {
    getTestData () {
      this.loading = true;
      getMacRecord(this.mac)
        .then(response => {
          // console.log(response.data);
          let data = response.data;

          this.alert = true;
          if (!data['status']) {
            this.alert_color = 'warning';
            this.loading = false;
            this.alert_message = data.payload.message;
          }
          else {
            this.dataSource = data.payload.data;
            this.loading = false;
            this.alert_color = 'success';
            this.alert_message = data.payload.message + ', ' + data.payload.time;
          }
        })
        .catch(e => {
          console.log(e);
          this.loading = false;
          this.alert = true;
          this.alert_color = 'red';
          this.alert_message = 'Service Error, Please Contact Genius Team!!';
        });
    },
  }
};
</script>


