<template>
<div id="themeSetting">

  <v-container :dark="$vuetify.dark">
    <v-layout column>
      <v-flex>
        <div class="color-option">
          <v-layout wrap>
            <label class="color-option--label flex xs6 pa-1" v-for="(option, index) in themeColorOptions" :key="index">
              <input type="radio" name="color" v-bind:value="option.name" v-model="themeColor" @click="setGeniusTheme(option.name)">
              <span class="color-option--item bg" :style="{background: option.colors.app+'!important'}">
                <span class="overlay">
                  <span class="material-icons">check</span>
                </span>
                <span class="color-option--item--header sideNav" :style="{background: option.colors.sideNav+'!important'}"></span>
                <span class="color-option--item--header mainNav" :style="{background: option.colors.mainNav+'!important'}"></span>
                <span class="sideMenu" :style="{background: option.colors.sideMenu+'!important'}"></span>
              </span>
            </label>
          </v-layout>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</div>
</template>

<script>
import colors from 'vuetify/es5/util/colors';
import themeList from '../api/themeOptions';
import { globalMixin } from '../util/mixins/globalMixins';
import {
  getUserTheme,
  setUserTheme,
} from '../pages/genius/api/getStyle';

export default {
  mixins: [globalMixin],
  data () {
    return {
      themeColor: 'purple',
      darkTheme: false,
      colors: colors,
      themeColorOptions: themeList,
    };
  },
  computed: {
    defaultTheme () {
      return {
        error: '#FF5252',
        info: '#2196F3',
        success: '#4CAF50',
        warning: '#FFC107'
      };
    }
  },
  watch: {
    'theme.name' (val) {
      // console.log('theme.name changed!');
      this.themeColor = val;
    },
    themeColor: {
      handler (val) {
        const theme = this.themeColorOptions.filter(v => v.name === val)[0];
        this.$vuetify.theme = Object.assign(JSON.parse(JSON.stringify(this.defaultTheme)), theme.colors);
        this.darkTheme = theme.isDark;
        this.setTheme(theme);
      },
      immediate: true
    },
    darkTheme: {
      handler (val) {
        this.$nextTick(() => {
          this.$vuetify.dark = val;
        });
      },
      immediate: true
    }
  },
  created () {
    this.getGeniusTheme();
  },
  methods: {
    setGeniusTheme (theme) {
      let username = this.$cookies.get('username');
      setUserTheme(username, theme)
        .then(response => {
          console.log('Set Genius Theme to ' + theme);
          // this.currentNotification = response.data.status;
        })
        .catch(e => {
          console.log(e);
        });
    },
    getGeniusTheme () {
      getUserTheme()
        .then(response => {
          this.themeColor = response.data.payload.data;
          // console.log('Get Theme ' + this.themeColor);
        })
        .catch(e => {
          console.log(e);
        });
    },
  },
};
</script>
<style lang="stylus" scoped>
.color-option
  &--label
    display: block
    cursor: pointer  
    & input[type="radio"] 
      display:none
      &+span 
        position: relative
        &>.overlay
          display: none;
          position: absolute
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0,0,0,.3);
          text-align: center;
          line-height: 30px;
          color: #fff;                
      &:checked+span>.overlay
        display:block  
    & .bg        
      background-color: #f1f1f1
  &--item
    overflow: hidden;
    display: block;
    box-shadow: 0 0 2px rgba(0,0,0,.1);
    margin-bottom: 15px;      
    &--header
      height: 10px
    &>span 
      display: block;
      float: left;
      width: 50%;
      height: 20px;          
</style>




