<template>
  <v-layout row wrap>
    <v-flex sm12>
      <v-widget title="MAC Block Management">
        <div slot="widget-content">
          <v-flex>
            <v-alert
              v-model="alert"
              dismissible
              :color="alert_color"
            >
              {{ alert_message }}
            </v-alert>
          </v-flex>
          <v-container fluid>
            <v-layout row>
              <v-flex xl4>
                <v-btn large v-show="macAccess"
                  color="primary"
                  @click="openDialogs = true; getBuListData()"
                >
                  Add Block
                </v-btn>
              </v-flex>
            </v-layout>

            <v-flex xl12>

              <v-card flat>
                <mac-block-table
                  id="mac-block-table"
                  title="MAC Block Details"
                  search=""
                  :username="name"
                  @disableMac="disableMac"
                  :testDataSource="dataSource"
                ></mac-block-table>
              </v-card>

            </v-flex>    

            <!-- Add Block dialogs -->
            <v-layout row justify-center>
              <v-dialog v-model="openDialogs" persistent max-width="1000">
                <v-card>
                  <v-card-title class="headline">Add MAC Block</v-card-title>

                  <v-card-text>
                    <v-layout row>
                      <v-flex xl3>
                        <v-select
                          v-model="bu"
                          label="Select BU"
                          :items="itemsBU"
                          outline
                        ></v-select>
                      </v-flex>
                      <v-flex xl3>
                        <v-select
                          v-model="project"
                          label="Select Project"
                          :items="itemsProject"
                          outline
                        ></v-select>
                      </v-flex>
                      <v-flex xl3>
                        <v-text-field 
                          v-model="product"
                          label="Product"
                          outline
                        >
                        </v-text-field>
                      </v-flex>
                      <v-flex xl3>
                        <v-select
                          v-model="setBlock"
                          label="Block Size For Calc"
                          :items="itemsBlock"
                          outline
                        ></v-select>
                      </v-flex>
                    </v-layout>
                    
                    <h4>Paste MAC Information into the dialog, following the example.</h4>
                    <v-divider></v-divider>
                    <v-card-text>
                      <pre>Allocated -> 131072</pre>
                      <pre>Starting  -> a4b439f40000</pre>
                      <pre>Ending    -> a4b439f5ffff</pre>
                    </v-card-text>
                  <v-divider></v-divider>
                  </v-card-text>
                  <v-card-actions>
                    <v-layout row>
                      <v-flex xl6>
                        <v-textarea outline v-model="macText">

                        </v-textarea>
                      </v-flex>
                      <v-flex xl6>
                        <v-card-text>
                          <h3 :class="blockColor">> Block Size: [{{ block_size }}]</h3>
                          <h3 :class="startColor">> MAC Start: [{{ mac_start }}]</h3>
                          <h3 :class="endColor">> MAC End: [{{ mac_end }}]</h3>
                        </v-card-text>
                      </v-flex>
                    </v-layout>
                  </v-card-actions>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="green darken-1" flat @click="openDialogs = false; this.macText = ''">Cancel</v-btn>
                    <v-btn color="green darken-1" flat @click="openDialogs = false; addMacBlockData()">Confirm</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-layout>
          </v-container>
        </div>
      </v-widget>
    </v-flex>
  </v-layout>
</template>

<script>
import VWidget from '@/components/VWidget';
import { getMacBlock, addMacBlock, getOuiList, getMacAccess, getBuList } from '../api/mac';
import MacBlockTable from '../components/MacBlockTable';

import store from '../store';

export default {
  components: {
    VWidget,
    MacBlockTable,
    store,
  },
  data () {
    return {
      name: '',
      macAccess: false,
      //
      loading: false,
      openDialogs: false,
      bu: '',
      itemsBU: ['WNBU', 'SRGBU'],
      project: '',
      product: '',
      itemsProject: ['MB', 'RB'],
      itemsBlock: [],
      macText: '',
      mac_start: '',
      mac_end: '',
      block_size: '',
      setBlock: 0,
      re: /\r\n|\n\r|\n|\r/g,
      // for alert
      alert: false,
      alert_color: 'warning',
      alert_message: '',
      dataSource: [],
      mac: '',
    };
  },

  computed: {
    startColor () {
      if (this.mac_start.length === 12) {
        return '';
      } else { return 'red' }
    },
    endColor () {
      if (this.mac_end.length === 12) {
        return '';
      } else { return 'red' }
    },
    blockColor () {
      if (this.block_size === '131072') {
        return '';
      } else { return 'red' }
    },
  },

  watch: {
    macText () {
      this.macText.split(this.re).forEach(element => {
        // console.log(element);
        if (element.indexOf('Starting') !== -1) {
          this.mac_start = element.split('->')[1].trim();
          // console.log('mac_start ' + this.mac_start);
        }
        if (element.indexOf('Ending') !== -1) {
          this.mac_end = element.split('->')[1].trim();
          // console.log('mac_end ' + this.mac_end);
        }
        if (element.indexOf('Allocated') !== -1) {
          this.block_size = element.split('->')[1].trim();
          // console.log('block_size ' + this.block_size);
        }
      });
    },
  },

  created () {
    this.username = this.$cookies.get('username');
    
    if (!this.username) {
      this.$router.replace('/genius/login');
    }
    this.name = this.username.split('(')[1].slice(0, -1);
    this.getMacBlockData();
    this.getMacAccessData();
  },

  methods: {
    getMacAccessData () {
      this.loading = true;
      getMacAccess(this.name)
        .then(response => {
          let data = response.data;
          // console.log(data);

          // this.alert = true;
          if (data.status) {
            this.macAccess = true;

          }
        })
        .catch(e => {
          console.log(e);
        });
    },

    getBuListData () {
      getBuList()
        .then(response => {
          let data = response.data;
          // console.log(data);
          if (!data.status) {
            this.alert_color = 'warning';
            this.alert = true;
            this.loading = false;
            this.alert_message = data.payload.message;
          }
          else {
            // this.alert = false;
            this.itemsBU = data.payload.data.bus;
            this.itemsProject = data.payload.data.projects;
            this.itemsBlock = data.payload.data.blocks;
          }
        })
        .catch(e => {
          console.log(e);
          this.loading = false;
          this.alert = true;
          this.alert_color = 'red';
          this.alert_message = 'Get BU Error, Please Contact Genius Team.';
        });
    },

    getMacBlockData () {
      this.loading = true;
      getMacBlock()
        .then(response => {
          let data = response.data;
          // console.log(data);

          if (!data.status) {
            this.alert_color = 'warning';
            this.alert = true;
            this.loading = false;
            this.alert_message = data.payload.message;
          }
          else {
            this.dataSource = data.payload.data;
            // this.loading = false;
            // this.alert = false;
            // this.alert_color = 'success';
            // this.alert_message = data.payload.message + ', ' + data.payload.time;
          }
        })
        .catch(e => {
          console.log(e);
          this.loading = false;
          this.alert = true;
          this.alert_color = 'red';
          this.alert_message = 'Service Error, Please Contact Genius Team.';
        });
    },

    addMacBlockData () {
      this.loading = true;
      let data = {
        'bu': this.bu,
        'project': this.project,
        'product': this.product,
        'mac_start': this.mac_start,
        'mac_end': this.mac_end,
        'block': this.block_size,
        'block_set': this.setBlock,
        'username': this.name,
      };
      addMacBlock(data)
        .then(response => {
          let data = response.data;
          // console.log(data);
          this.macText = '';
          this.alert = true;
          if (!data.status) {
            this.alert_color = 'warning';
            this.loading = false;
            this.alert_message = data.payload.message;
          }
          else {
            this.loading = false;
            this.alert_color = 'success';
            this.alert_message = data.payload.message + ', ' + data.payload.time;
            this.getMacBlockData();
          }
        })
        .catch(e => {
          console.log(e);
          this.loading = false;
          this.alert = true;
          this.alert_color = 'red';
          this.alert_message = 'Service Error, Please Contact Genius Team.';
        });
    },
    disableMac (data) {
      if (!data.status) {
        this.alert_color = 'warning';
        this.loading = false;
        this.alert_message = data.payload.message;
      }
      else {
        this.loading = false;
        this.alert_color = 'success';
        this.alert_message = data.payload.message + ', ' + data.payload.time;
        this.getMacBlockData();
      }
    },
  }
};
</script>


