function getIpAddress () {
  let hostname = window.location.hash.split('=')[1];
  if (!hostname) { 
    hostname = window.location.hostname;
  }
  // console.log(hostname);
  // hostname = '10.167.16.58';
  // hostname = '10.167.16.44';
  return 'https://' + hostname + '/api';
}

export {
  getIpAddress
};



