import axios from 'axios';
import { getIpAddress } from './getIpAddress';

function getDataSummary (params) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/vision/',
    {
      'data_type': params.data_type, 'start_date': params.start_date, 'end_date': params.end_date, 'uuttype': params.uuttype, 
      'sernum': params.sernum, 'area': params.area, 'machine': params.machine, 'mode': params.mode, 'result': params.result, 
      'test': params.test, 'field': params.field, 'service': 'get-data-summary'
    }
  );

}

function getDataDetails (time_stamp, date_stamp, data_type) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/vision/',
    { 'time_stamp': time_stamp, 'date_stamp': date_stamp, 'data_type': data_type, 'service': 'get-data-details' });
}

function getDataExcel (time_stamp, data_type, result) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/vision/',
    { 'time_stamp': time_stamp, 'data_type': data_type, 'result': result, 'service': 'get-data-excel' });
}

function deleteDataCache (time_stamp) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/vision/',
    { 'time_stamp': time_stamp, 'service': 'delete-data-cache' });
}


export {
  getDataSummary,
  getDataDetails,
  getDataExcel,
  deleteDataCache,
};


