<template>
  <v-content id="login" class="primary">
    <v-container fluid fill-height>
      <v-layout justify-center>
        <v-flex xs12 sm6 md6 lg6>
          <v-card class="elevation-1 pa-3">
            <v-card-text>
              <div class="layout column align-center">
                <!-- <img src="/static/logo.png" alt="Vue Material Admin" width="120" height="120"> -->
                <h1 class="flex my-4 primary--text">Change Password</h1>
                <h3 class="primary--text">{{ username1 }}</h3>
              </div>                
              <v-form>
                <v-text-field append-icon="lock" name="password" label="New Password" id="password" type="password" v-model="model.password"></v-text-field>
                <v-text-field append-icon="lock" name="password1" label="New Password Again" id="password1" type="password" v-model="model.password1"></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-btn block color="primary" @click="changePassword1" :loading="loading">Confirm</v-btn>
            </v-card-actions>
          </v-card>
          <v-dialog
            v-model="openDialog"
            max-width="590"
          >
          <v-card>
            <v-card-text>
              <h4>{{ message }}</h4>
            </v-card-text>
          </v-card>
        </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
import { changePassword } from '../api/gac';

export default {
  data: () => ({
    loading: false,
    message: '',
    openDialog: false,
    username: '',
    username1: '',
    model: {
      password: '',
      password1: ''
    }
  }),
  mounted () {
    this.username1 = this.$cookies.get('username');
    if (!this.username1) {
      this.openDialog = true;
      this.message = 'Should Login Firstly';
      setTimeout(() => {
        this.$router.push('/genius/login');
      }, 3000);
      return false;
    }
    if (this.username1 === 'genius' || this.username1 === 'engineer') {
      this.openDialog = true;
      this.message = this.username1 + ' does NOT Support Password Change';
      setTimeout(() => {
        this.$router.push('/');
      }, 5000);
      return false;
    }
    this.username = this.username1.split('(')[1].split(')')[0];
  },

  methods: {
    changePassword1 () {
      // console.log(this.model.password);
      // console.log(this.model.password1);
      if (this.model.password !== this.model.password1) {
        this.openDialog = true;
        this.message = 'Two passwords do NOT match';
        return false;
      }
      this.loading = true;
      changePassword(this.username, this.model.password)
        .then(response => {
          this.openDialog = true;
          console.log(response.data);
          if (response.data.status) {
            this.message = response.data.payload.message;
            setTimeout(() => {
              this.$router.push('/genius/login');
            }, 5000);
          } else {
            this.message = response.data.payload.message;
            return false;
          }          
        })
        .catch(e => {
          // console.log(e);
          this.openDialog = true;
          this.message = 'GAC Service Errors';
        });
      this.loading = false;
    }
  }

};
</script>
<style scoped lang="css">
  #login {
    height: 50%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    z-index: 0;
  }
</style>



