const vision_router = [
  {
    path: '/vision',
    component: () => import('./Vision.vue'),
    meta: {
      // theme: 'indigo',
    },
    children: [
      {
        path: 'basic',
        meta: {
          breadcrumb: false
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/vision/pages/basic.vue`
        )
      },
      {
        path: 'advanced',
        meta: {
          breadcrumb: false,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/vision/pages/advanced2.vue`
        )
      },
      {
        path: 'mac',
        meta: {
          breadcrumb: false,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/vision/pages/mac.vue`
        )
      },
      {
        path: 'manage-mac',
        meta: {
          breadcrumb: false,
        },
        component: () => import(
          /* webpackChunkName: "routes" */
          /* webpackMode: "lazy-once" */
          `@/pages/vision/pages/MacManage.vue`
        )
      },
    ]
  },
];

export default vision_router;



