<template>
  <v-content id="login" class="primary">
    <v-container fluid fill-height>
      <v-layout justify-center>
        <v-flex xs12 sm6 md6 lg6>
          <v-card class="elevation-1 pa-3">
            <v-card-text>
              <div class="layout column align-center">
                <!-- <img src="/static/logo.png" alt="Vue Material Admin" width="120" height="120"> -->
                <h1 class="flex my-4 primary--text">Set Identifier for Future Password Reset</h1>
                <h3 class="primary--text">{{ username }}</h3>
              </div>                
              <v-form>
                <v-menu
                  class="pr-2"
                  ref="setStatDate"
                  lazy
                  :close-on-content-click="false"
                  v-model="startDateMenu"
                  transition="scale-transition"
                  offset-y
                  full-width
                  :nudge-bottom="-22"
                  max-width="290px"
                  :return-value.sync="model.onboard"
                >
                  <v-text-field
                    slot="activator"
                    label="On Board Day(YYYY-MM-DD)"
                    v-model="model.onboard"
                    append-icon="event"
                    readonly
                  ></v-text-field>
                  <v-date-picker v-model="model.onboard" no-title scrollable>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="startDateMenu = false">Cancel</v-btn>
                    <v-btn flat color="primary" @click="$refs.setStatDate.save(model.onboard)">OK</v-btn>
                  </v-date-picker>
                </v-menu>
                <v-text-field append-icon="lock" label="Identifier" type="text" :rules="rules" v-model="model.identifier"></v-text-field>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-btn block color="primary" @click="setIdentifier1" :loading="loading">Confirm</v-btn>
            </v-card-actions>
          </v-card>
          <v-dialog
            v-model="openDialog"
            max-width="590"
          >
          <v-card>
            <v-card-text>
              <h4>{{ message }}</h4>
            </v-card-text>
          </v-card>
        </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>
import { setIdentifier } from '../api/gac';

export default {
  data: () => ({
    loading: false,
    startDateMenu: false,
    message: '',
    username: '',
    openDialog: false,
    rules: [v => v.length > 5 || 'Min 6 characters'],
    model: {
      onboard: '',
      identifier: ''
    }
  }),
  mounted () {
    this.username = this.$cookies.get('username');
  },

  methods: {
    setIdentifier1 () {
      this.message = '';
      if (this.model.identifier.length < 5) {
        this.message = 'Identifier length should be 6 or above';
      }
      if (this.message) {
        this.openDialog = true;
        return false;
      }
      this.loading = true;
      // console.log(this.model.onboard);
      // console.log(this.model.identifier);
      setIdentifier(this.username.split('(')[1].replace(')', ''), this.model.onboard, this.model.identifier)
        .then(response => {
          this.openDialog = true;
          this.loading = false;
          // console.log(response.data);
          if (response.data.status) {
            this.message = response.data.payload.message;

            setTimeout(() => {
              this.$router.push('/genius');
            }, 3000);
          } else {
            this.message = response.data.payload.message;
            return false;
          } 
        })
        .catch(e => {
          // console.log(e);
          this.openDialog = true;
          this.loading = false;
          this.message = 'GAC Service Errors';
        });
    }
  }

};
</script>
<style scoped lang="css">
  #login {
    height: 50%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    z-index: 0;
  }
</style>



