<template>
  <v-bottom-sheet 
    v-model="openQuestion"
    :persistent="true"
    :hide-overlay="true"
    full-width
    color="primary"
  >
    <v-container grid-list-xl fluid pa-0>
      <v-layout row wrap>
        <v-flex lg12 xs12 sm12 md12>
          <v-list>
            <!-- image -->
            <v-flex lg12 xs12 sm12 md12 mt-0 pb-0 v-if="image">
              <img
                v-bind:src="image"
                v-bind:alt="'Could not display ' + image"
                style="height: 320px; width: 100%"
              />
            </v-flex>
            <!-- question -->
            <v-flex lg12 xs12 sm12 md12 mt-0 pb-0>
              <v-tooltip right>
                <v-icon
                  @click="closeQuestion"
                  slot="activator"
                  color="primary"
                  dark
                >clear</v-icon>
                <span>To Close Question</span>
              </v-tooltip>
              <time-counter :start="startCounter" :stop="stopCounter"></time-counter>
              <h3>[ <span class="font-weight-regular red--text">{{ container }}</span> ]: {{ title }}</h3>
            </v-flex>
            <!-- input -->
            <v-flex lg12 xs12 sm12 md12 pt-0 pb-0 v-if="type === 'text'">
              <v-text-field
                style="font-size:22px;"
                label="Type Your Input Here."
                autofocus
                outline
                clearable
                :type="textType"
                v-model="userInput"
                v-on:keyup.enter="onEnter"
              >
                <v-btn slot="append-outer" large color="primary" style="top: -16px" @click="submitUserInput">
                  <v-icon left>send</v-icon>
                  Submit
                </v-btn>
              </v-text-field>
            </v-flex>
            <!-- select -->
            <v-flex lg12 xs12 sm12 md12 pt-0 pb-0 v-if="type === 'select'">
              <v-select
                :items="options"
                style="font-size:22px;"
                label="Select Your Option Here."
                outline
                clearable
                :multiple="multiple"
                v-model="userInput"
                :error-messages="selectError"
              >
              
                <template v-slot:prepend-item v-if="multiple">
                  <v-list-tile
                  ripple
                    @click="toggleSelect"
                  >
                    <v-list-tile-action>
                      <v-icon :color="userInput.length > 0 ? 'indigo darken-4' : ''">{{ selectIcon }}</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                      <v-list-tile-title>Select All</v-list-tile-title>
                    </v-list-tile-content>
                  </v-list-tile>
                  <v-divider class="mt-2"></v-divider>
                </template>

                <v-btn slot="append-outer" large color="primary" style="top: -16px" @click="submitUserInput">
                  <v-icon left>send</v-icon>
                  Submit
                </v-btn>
              </v-select>
            </v-flex>
          </v-list>
        </v-flex>
      </v-layout>
    </v-container>
  </v-bottom-sheet>
</template>

<script>
import TimeCounter from '../components/TimeCounter';

export default {
  components: {
    TimeCounter,
  },
  props: ['title', 'open', 'type', 'options', 'image', 'container', 'visible', 'multiple'],
  data () {
    return {
      userInput: Object,
      stopCounter: false,
      startCounter: false,
      selectError: '',
    };
  },
  computed: {
    openQuestion () {
      return this.open;
    },
    textType () {
      if (this.visible === 'YES') {
        return 'text';
      } else {
        return 'password';
      }
    },
    likesAllOptions () {
      return this.userInput.length === this.options.length;
    },
    likesSomeOptions () {
      return this.userInput.length > 0 && !this.likesAllOptions;
    },
    selectIcon () {
      if (this.likesAllOptions) return 'check_box';
      if (this.likesSomeOptions) return 'indeterminate_check_box';
      return 'check_box_outline_blank';
    }
  },
  watch: {
    title () {
      this.startCounter = !this.startCounter;
    }
  },
  mounted () {
    if (this.type === 'text') {
      this.userInput = '';
    } else {
      this.userInput = [];
    }
  },
  methods: {
    submitUserInput () {
      // this.openQuestion = false;
      // this.stopCounter = !this.stopCounter;
      // console.log(this.userInput);
      if (this.type === 'select' && this.userInput.length < 1) {
        this.selectError = 'Must Select one at least !';
        return false;
      }
      this.$emit('answerQuestion', this.userInput, this.container);
      this.userInput = '';
    },
    onEnter () {
      this.submitUserInput();
    },
    closeQuestion () {
      this.$emit('closeQuestion');
      this.userInput = '';
    },
    toggleSelect () {
      this.$nextTick(() => {
        if (this.likesAllOptions) {
          this.userInput = [];
        } else {
          this.userInput = this.options.slice();
        }
      });
    }
  }
};
</script>

<style lang='stylus' scoped>
</style>


