import axios from 'axios';
import { getIpAddress } from './getIpAddress';


function validateUsernameApi (username) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/acpro/validate-gac-username/', 
    { 'username': username },
  );
}

function validatePasswordApi (username, password) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/acpro/get-gac-authentication/', 
    { 'username': username, 'password': password },
  );
}

function getRegister (username, password, first_name, last_name, chinese_name, onboard, identifier) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/acpro/',
    { 
      'username': username, 'password': password, 
      'chinese_name': chinese_name, 'first_name': first_name, 'last_name': last_name, 'onboard': onboard, 'identifier': identifier,
      'service': 'register-gac-account',
    }
  );
}

function changePassword (username, password) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/acpro/', { 'username': username, 'password': password, 'service': 'change-gac-password' });
}

function resetPassword (username, onboard, identifier) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/acpro/', { 'username': username, 'onboard': onboard, 'identifier': identifier, 'service': 'reset-gac-password' });
}

function setIdentifier (username, onboard, identifier) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/acpro/', { 'username': username, 'onboard': onboard, 'identifier': identifier, 'service': 'set-gac-identifier' });
}

function editProfile (username, email) {
  const ipAddress = getIpAddress();
  return axios.post(ipAddress + '/acpro/update-gac-profile/', 
    { 'username': username, 'email': email });
}

function getProjectAccess (username) {
  const ipAddress = getIpAddress();
  return axios.get(ipAddress + '/acpro/get-gac-access/?username=' + username);
}
function getResponseAccess (username, result, role) {
  const ipAddress = getIpAddress();
  return axios.get(`${ipAddress}/acpro/get-gac-response-access/`, 
    { params: { username: username, result: result, role: role }}
  );
}

function getUserSubordinates (username) {
  const ipAddress = getIpAddress();
  return axios.get(`${ipAddress}/acpro/get-user-subordinates/`, {
    params: {
      username: username,
    }
  });
}
function disableAccessAccount (username, admin, leaveDate) {
  const ipAddress = getIpAddress();
  return axios.post(`${ipAddress}/disable-gac-user/`,
    { username: username, admin: admin, leave: leaveDate });
}

export {
  validateUsernameApi,
  validatePasswordApi,
  getRegister,
  changePassword,
  resetPassword,
  editProfile,
  getUserSubordinates,
  disableAccessAccount,
  setIdentifier,
};



