<template>
  <v-widget :title="title">
    <div slot="widget-content">
      <v-layout row wrap>   
        <v-flex xl12 pa-0>
          <v-card>

            <v-card-text class="pa-0">
              <v-data-table
                :headers="testData.headers"
                :pagination.sync="testData.pagination"
                :search="searchFilter"
                :items="testDataContent"
                class="elevation-1"
                item-key="recttime"
                >
                <template slot="items" slot-scope="props">
                  <tr>
                    <td>{{ props.item.created_at.replace('T', ' ').split('.')[0] }}</td>
                    <td>{{ props.item.mac }}</td>
                    <td>{{ props.item.block_size }}</td>
                    <td>{{ props.item.sernum }}</td>
                    <td>{{ props.item.uuttype }}</td>
                    <td>{{ props.item.area }}</td>
                    <td>{{ props.item.bu }}</td>
                    <td>{{ props.item.project }}</td>
                    <td>{{ props.item.product }}</td>
                    <td>{{ props.item.machine }}</td>
                    <td>{{ props.item.container }}</td>
                    <td>{{ props.item.username }}</td>
                  </tr>
                </template>
              </v-data-table>
              <json-to-excel :jsonData="testDataSource"></json-to-excel>
            </v-card-text>
          </v-card>
        </v-flex>             
      </v-layout>
    </div>
  </v-widget>
</template>

<script>
import VWidget from '@/components/VWidget';
import JsonToExcel from '../components/JsonToExcel';
import store from '../../vision/store';

export default {
  components: {
    VWidget,
    JsonToExcel,
    store,
  },
  props: ['search', 'title', 'testDataSource'],
  data () {
    return {
      message: '',
      openDialogs: false,
      testData: {
        pagination: {
          descending: true,
        },
        headers: [
          {
            text: 'Record Time',
            value: 'created_at',
          },
          {
            text: 'MAC Address',
            value: 'mac'
          },
          {
            text: 'Block Size',
            value: 'block_size'
          },
          {
            text: 'Serial Number',
            value: 'sernum'
          },
          {
            text: 'UUT Type',
            value: 'uuttype'
          },
          {
            text: 'Area',
            value: 'area'
          },
          {
            text: 'BU',
            value: 'bu'
          },
          {
            text: 'Project',
            value: 'project'
          },
          {
            text: 'Product',
            value: 'product'
          },
          {
            text: 'Machine',
            value: 'machine'
          },
          {
            text: 'Container',
            value: 'container'
          },
          {
            text: 'Username',
            value: 'username'
          },
        ],
        content: []
      },
    };
  },
  computed: {
    testDataContent () {
      return this.testDataSource || [];
    },
    searchFilter () {
      return this.search;
    }
  },
};
</script>

<style lang='stylus' scoped>
</style>


